'use client';
import React, { useState } from 'react';
import { 
  ArrowLeft, Bell, Mail, Smartphone, Shield, 
  AlertCircle, Info, CheckCircle, Save, Volume2,
  Clock, MessageSquare
} from 'lucide-react';
import { useRouter } from 'next/navigation';

export default function NotificationSettingsPage() {
  const router = useRouter();
  
  const [settings, setSettings] = useState({
    // Notification Channels
    emailNotifications: true,
    pushNotifications: true,
    smsNotifications: false,
    
    // Notification Types
    securityAlerts: true,
    deviceUpdates: true,
    complianceWarnings: true,
    systemAnnouncements: true,
    resourceUpdates: false,
    
    // Priority Settings
    criticalOnly: false,
    includeWarnings: true,
    includeInfo: true,
    
    // Delivery Preferences
    instantDelivery: true,
    dailyDigest: false,
    weeklyDigest: false,
    
    // Quiet Hours
    quietHoursEnabled: false,
    quietHoursStart: '22:00',
    quietHoursEnd: '08:00',
    
    // Sound Settings
    soundEnabled: true,
    vibrationEnabled: true
  });

  const [saveStatus, setSaveStatus] = useState('');

  const handleToggle = (key) => {
    setSettings(prev => ({ ...prev, [key]: !prev[key] }));
  };

  const handleTimeChange = (key, value) => {
    setSettings(prev => ({ ...prev, [key]: value }));
  };

  const handleSave = () => {
    setSaveStatus('saving');
    setTimeout(() => {
      setSaveStatus('saved');
      setTimeout(() => setSaveStatus(''), 3000);
    }, 1000);
  };

  const ToggleSwitch = ({ enabled, onChange, disabled = false }) => (
    <button
      onClick={onChange}
      disabled={disabled}
      className={`relative inline-flex h-6 w-11 items-center rounded-full transition ${
        enabled ? 'bg-blue-600' : 'bg-gray-300'
      } ${disabled ? 'opacity-50 cursor-not-allowed' : 'cursor-pointer'}`}
    >
      <span
        className={`inline-block h-4 w-4 transform rounded-full bg-white transition ${
          enabled ? 'translate-x-6' : 'translate-x-1'
        }`}
      />
    </button>
  );

  return (
    <div className="min-h-screen bg-gray-50 p-6">
      <div className="max-w-4xl mx-auto">
        {/* Back Button */}
        <button
          onClick={() => router.back()}
          className="flex items-center gap-2 text-gray-600 hover:text-gray-900 mb-6"
        >
          <ArrowLeft className="w-5 h-5" />
          Back to Notifications
        </button>

        {/* Page Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">Notification Settings</h1>
          <p className="text-gray-600 text-lg">Customize how you receive alerts and updates</p>
        </div>

        {/* Save Status */}
        {saveStatus && (
          <div className={`mb-6 p-4 rounded-lg ${
            saveStatus === 'saved' 
              ? 'bg-green-50 border border-green-200' 
              : 'bg-blue-50 border border-blue-200'
          }`}>
            <div className="flex items-center gap-2">
              {saveStatus === 'saved' ? (
                <>
                  <CheckCircle className="w-5 h-5 text-green-600" />
                  <span className="text-green-900 font-medium">Settings saved successfully!</span>
                </>
              ) : (
                <>
                  <Info className="w-5 h-5 text-blue-600" />
                  <span className="text-blue-900 font-medium">Saving settings...</span>
                </>
              )}
            </div>
          </div>
        )}

        <div className="space-y-6">
          {/* Notification Channels */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <Bell className="w-6 h-6 text-blue-600" />
              <h2 className="text-2xl font-bold text-gray-900">Notification Channels</h2>
            </div>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <Mail className="w-5 h-5 text-gray-600" />
                  <div>
                    <p className="font-semibold text-gray-900">Email Notifications</p>
                    <p className="text-sm text-gray-600">Receive alerts via email</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.emailNotifications}
                  onChange={() => handleToggle('emailNotifications')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <Smartphone className="w-5 h-5 text-gray-600" />
                  <div>
                    <p className="font-semibold text-gray-900">Push Notifications</p>
                    <p className="text-sm text-gray-600">Browser and mobile push alerts</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.pushNotifications}
                  onChange={() => handleToggle('pushNotifications')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <MessageSquare className="w-5 h-5 text-gray-600" />
                  <div>
                    <p className="font-semibold text-gray-900">SMS Notifications</p>
                    <p className="text-sm text-gray-600">Text messages for critical alerts</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.smsNotifications}
                  onChange={() => handleToggle('smsNotifications')}
                />
              </div>
            </div>
          </div>

          {/* Notification Types */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <AlertCircle className="w-6 h-6 text-orange-600" />
              <h2 className="text-2xl font-bold text-gray-900">Notification Types</h2>
            </div>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <Shield className="w-5 h-5 text-red-600" />
                  <div>
                    <p className="font-semibold text-gray-900">Security Alerts</p>
                    <p className="text-sm text-gray-600">Critical security updates and threats</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.securityAlerts}
                  onChange={() => handleToggle('securityAlerts')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <Smartphone className="w-5 h-5 text-blue-600" />
                  <div>
                    <p className="font-semibold text-gray-900">Device Updates</p>
                    <p className="text-sm text-gray-600">Device sync, enrollment, and status changes</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.deviceUpdates}
                  onChange={() => handleToggle('deviceUpdates')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <AlertCircle className="w-5 h-5 text-yellow-600" />
                  <div>
                    <p className="font-semibold text-gray-900">Compliance Warnings</p>
                    <p className="text-sm text-gray-600">Policy violations and compliance issues</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.complianceWarnings}
                  onChange={() => handleToggle('complianceWarnings')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <Info className="w-5 h-5 text-blue-600" />
                  <div>
                    <p className="font-semibold text-gray-900">System Announcements</p>
                    <p className="text-sm text-gray-600">Maintenance, updates, and news</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.systemAnnouncements}
                  onChange={() => handleToggle('systemAnnouncements')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div className="flex items-center gap-3">
                  <Info className="w-5 h-5 text-green-600" />
                  <div>
                    <p className="font-semibold text-gray-900">Resource Updates</p>
                    <p className="text-sm text-gray-600">New apps, documents, and resources</p>
                  </div>
                </div>
                <ToggleSwitch
                  enabled={settings.resourceUpdates}
                  onChange={() => handleToggle('resourceUpdates')}
                />
              </div>
            </div>
          </div>

          {/* Priority Settings */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <AlertCircle className="w-6 h-6 text-red-600" />
              <h2 className="text-2xl font-bold text-gray-900">Priority Settings</h2>
            </div>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Critical Alerts Only</p>
                  <p className="text-sm text-gray-600">Only receive critical priority notifications</p>
                </div>
                <ToggleSwitch
                  enabled={settings.criticalOnly}
                  onChange={() => handleToggle('criticalOnly')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Include Warnings</p>
                  <p className="text-sm text-gray-600">Receive warning priority notifications</p>
                </div>
                <ToggleSwitch
                  enabled={settings.includeWarnings}
                  onChange={() => handleToggle('includeWarnings')}
                  disabled={settings.criticalOnly}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Include Info</p>
                  <p className="text-sm text-gray-600">Receive informational notifications</p>
                </div>
                <ToggleSwitch
                  enabled={settings.includeInfo}
                  onChange={() => handleToggle('includeInfo')}
                  disabled={settings.criticalOnly}
                />
              </div>
            </div>
          </div>

          {/* Delivery Preferences */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <Clock className="w-6 h-6 text-purple-600" />
              <h2 className="text-2xl font-bold text-gray-900">Delivery Preferences</h2>
            </div>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Instant Delivery</p>
                  <p className="text-sm text-gray-600">Receive notifications immediately</p>
                </div>
                <ToggleSwitch
                  enabled={settings.instantDelivery}
                  onChange={() => handleToggle('instantDelivery')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Daily Digest</p>
                  <p className="text-sm text-gray-600">One summary email per day at 9 AM</p>
                </div>
                <ToggleSwitch
                  enabled={settings.dailyDigest}
                  onChange={() => handleToggle('dailyDigest')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Weekly Digest</p>
                  <p className="text-sm text-gray-600">One summary email every Monday at 9 AM</p>
                </div>
                <ToggleSwitch
                  enabled={settings.weeklyDigest}
                  onChange={() => handleToggle('weeklyDigest')}
                />
              </div>
            </div>
          </div>

          {/* Quiet Hours */}
          <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-6">
            <div className="flex items-center gap-3 mb-6">
              <Volume2 className="w-6 h-6 text-indigo-600" />
              <h2 className="text-2xl font-bold text-gray-900">Quiet Hours</h2>
            </div>
            
            <div className="space-y-4">
              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Enable Quiet Hours</p>
                  <p className="text-sm text-gray-600">Mute non-critical notifications during specified times</p>
                </div>
                <ToggleSwitch
                  enabled={settings.quietHoursEnabled}
                  onChange={() => handleToggle('quietHoursEnabled')}
                />
              </div>

              {settings.quietHoursEnabled && (
                <div className="p-4 bg-blue-50 border border-blue-200 rounded-lg">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        Start Time
                      </label>
                      <input
                        type="time"
                        value={settings.quietHoursStart}
                        onChange={(e) => handleTimeChange('quietHoursStart', e.target.value)}
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 mb-2">
                        End Time
                      </label>
                      <input
                        type="time"
                        value={settings.quietHoursEnd}
                        onChange={(e) => handleTimeChange('quietHoursEnd', e.target.value)}
                        className="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                      />
                    </div>
                  </div>
                  <p className="text-sm text-blue-900 mt-3">
                    <Info className="w-4 h-4 inline mr-1" />
                    Critical security alerts will still be delivered during quiet hours
                  </p>
                </div>
              )}

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Sound Enabled</p>
                  <p className="text-sm text-gray-600">Play sound for notifications</p>
                </div>
                <ToggleSwitch
                  enabled={settings.soundEnabled}
                  onChange={() => handleToggle('soundEnabled')}
                />
              </div>

              <div className="flex items-center justify-between p-4 bg-gray-50 rounded-lg">
                <div>
                  <p className="font-semibold text-gray-900">Vibration Enabled</p>
                  <p className="text-sm text-gray-600">Vibrate for mobile notifications</p>
                </div>
                <ToggleSwitch
                  enabled={settings.vibrationEnabled}
                  onChange={() => handleToggle('vibrationEnabled')}
                />
              </div>
            </div>
          </div>

          {/* Save Button */}
          <div className="flex justify-end gap-4">
            <button
              onClick={() => router.back()}
              className="px-6 py-3 border border-gray-300 text-gray-700 rounded-lg hover:bg-gray-50 transition font-medium"
            >
              Cancel
            </button>
            <button
              onClick={handleSave}
              disabled={saveStatus === 'saving'}
              className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 transition font-medium flex items-center gap-2 disabled:bg-blue-400"
            >
              <Save className="w-5 h-5" />
              {saveStatus === 'saving' ? 'Saving...' : 'Save Settings'}
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}